.logomotiv {

	&__promo {
		// height: 500px;
		padding: 40px 0 40px;
		@include make-container();
		background-color: #1A1A1A;
		background-image: svg-load('../img/logomotiv__pattern.svg');
		background-size: 9px 9px;
		background-repeat: repeat;
		margin-top: 40px;
		// background-position: center;
		@media ($min-md) { margin-top: 70px;}

	}

	&__promo-img-inner {
		margin: 0 auto;
		max-width: 920px;
	}

	&__video-inner {
		@include make-container();
		min-width: 320px;
		max-width: 1200px;
	}

	&__video {
		@include make-col(0px);
		flex: 100%;

		@media ($min-lg) {
			padding-left: 15px;
			padding-right: 15px;
		}

		& video { max-width: 100%;}

	}





	///// Case /////

	&__case {
		@include make-container();
		min-width: 320px;
		max-width: 1200px;
		padding-bottom: 50px;

		@media ($min-md){ padding-bottom: 100px;}
	}

	&__case-compare {
		@include make-col(30px);
		flex: 100%;
		margin: 0 auto;		
		position: relative;

		&--md {
			background-image: url('../img/logomotiv__case-bg.png');
			background-size: 100% auto;
			background-repeat: no-repeat;
			background-position: top;
			// padding-top: 120px;
			// padding-bottom: 60px;
			max-width: 1170px;
			z-index: 2;

			&::after {
				content: '';
				width: 100%;
				height: 30%;
				display: block;
				position: absolute;
				left: 0;
				bottom: 0;
				z-index: 1;
				background-image: url('../img/logomotiv__case-bg.png');
				background-size: 100% auto;
				background-repeat: no-repeat;
				background-position: bottom;
			}
		}

		

		// & > div {
		// 	max-width: 945px;
		// 	margin: 0 auto;
		// }
	}

	&__compare-inner {
		margin: 0 auto;
		z-index: 3;
		position: relative;
	}


	&__case-compare--md &__compare-inner {
		max-width: 1005px;
		padding: 75px 30px 50px;

		@media ($min-lg) { padding: 90px 30px 60px;}
		@media ($min-lg) { padding: 90px 30px 60px;}
	}

}











///// COMPARE SLIDER /////

.compare-slider {
	 // & img {
	 //     object-fit: cover;
	 // }
}

.twentytwenty-horizontal .twentytwenty-handle:before, .twentytwenty-horizontal .twentytwenty-handle:after, .twentytwenty-vertical .twentytwenty-handle:before, .twentytwenty-vertical .twentytwenty-handle:after {
	content: " ";
	display: block;
	background: white;
	position: absolute;
	z-index: 30;
	-webkit-box-shadow: 0px 0px 12px rgba(51, 51, 51, 0.5);
	-moz-box-shadow: 0px 0px 12px rgba(51, 51, 51, 0.5);
	box-shadow: 0px 0px 12px rgba(51, 51, 51, 0.5); 
}

.twentytwenty-horizontal .twentytwenty-handle:before, .twentytwenty-horizontal .twentytwenty-handle:after {
	width: 2px;
	height: 9999px;
	left: 50%;
	margin-left: -1px; 
}

.twentytwenty-vertical .twentytwenty-handle:before, .twentytwenty-vertical .twentytwenty-handle:after {
	width: 9999px;
	height: 2px;
	top: 50%;
	margin-top: -1px; 
}

.twentytwenty-before-label, .twentytwenty-after-label, .twentytwenty-overlay {
	position: absolute;
	top: 0;
	width: 100%;
	height: 100%; 
}

.twentytwenty-before-label, .twentytwenty-after-label, .twentytwenty-overlay {
	-webkit-transition-duration: 0.5s;
	-moz-transition-duration: 0.5s;
	transition-duration: 0.5s; 
}

.twentytwenty-before-label, .twentytwenty-after-label {
	-webkit-transition-property: opacity;
	-moz-transition-property: opacity;
	transition-property: opacity; 
}

.twentytwenty-before-label:before, .twentytwenty-after-label:before {
	color: white;
	font-size: 13px;
	letter-spacing: 0.1em; 
}

.twentytwenty-before-label:before, .twentytwenty-after-label:before {
	position: absolute;
	background: rgba(255, 255, 255, 0.2);
	line-height: 38px;
	padding: 0 20px;
	-webkit-border-radius: 2px;
	-moz-border-radius: 2px;
	border-radius: 2px; 
}

.twentytwenty-horizontal .twentytwenty-before-label:before, .twentytwenty-horizontal .twentytwenty-after-label:before {
	top: 50%;
	margin-top: -19px; 
}

.twentytwenty-vertical .twentytwenty-before-label:before, .twentytwenty-vertical .twentytwenty-after-label:before {
	left: 50%;
	margin-left: -45px;
	text-align: center;
	width: 90px; 
}

.twentytwenty-left-arrow, .twentytwenty-right-arrow, .twentytwenty-up-arrow, .twentytwenty-down-arrow {
	width: 0;
	height: 0;
	border: 6px inset transparent;
	position: absolute; 
}

.twentytwenty-left-arrow, .twentytwenty-right-arrow {
	top: 50%;
	margin-top: -6px; 
}

.twentytwenty-up-arrow, .twentytwenty-down-arrow {
	left: 50%;
	margin-left: -6px; 
}

.twentytwenty-container {
	-webkit-box-sizing: content-box;
	-moz-box-sizing: content-box;
	box-sizing: content-box;
	z-index: 0;
	overflow: hidden;
	position: relative;
	-webkit-user-select: none;
	-moz-user-select: none; 
}
	.twentytwenty-container img {
	max-width: 100%;
	position: absolute;
	top: 0;
	display: block; 
}
	.twentytwenty-container.active .twentytwenty-overlay, .twentytwenty-container.active :hover.twentytwenty-overlay {
	background: rgba(0, 0, 0, 0); 
}
	.twentytwenty-container.active .twentytwenty-overlay .twentytwenty-before-label,
	.twentytwenty-container.active .twentytwenty-overlay .twentytwenty-after-label, .twentytwenty-container.active :hover.twentytwenty-overlay .twentytwenty-before-label,
	.twentytwenty-container.active :hover.twentytwenty-overlay .twentytwenty-after-label {
		opacity: 0; 
	}
	.twentytwenty-container * {
	-webkit-box-sizing: content-box;
	-moz-box-sizing: content-box;
	box-sizing: content-box; 
}

.twentytwenty-before-label {
	opacity: 0; 
}
	.twentytwenty-before-label:before {
	content: "Before"; 
}

.twentytwenty-after-label {
	opacity: 0; 
}
	.twentytwenty-after-label:before {
	content: "After"; 
}

.twentytwenty-horizontal .twentytwenty-before-label:before {
	left: 10px; 
}

.twentytwenty-horizontal .twentytwenty-after-label:before {
	right: 10px; 
}

.twentytwenty-vertical .twentytwenty-before-label:before {
	top: 10px; 
}

.twentytwenty-vertical .twentytwenty-after-label:before {
	bottom: 10px; 
}

.twentytwenty-overlay {
	-webkit-transition-property: background;
	-moz-transition-property: background;
	transition-property: background;
	background: rgba(0, 0, 0, 0);
	z-index: 25; 
}
	.twentytwenty-overlay:hover {
	background: rgba(0, 0, 0, 0.5); 
}
	.twentytwenty-overlay:hover .twentytwenty-after-label {
		opacity: 1; 
	}
	.twentytwenty-overlay:hover .twentytwenty-before-label {
		opacity: 1; 
	}

.twentytwenty-before {
	z-index: 20; 
}

.twentytwenty-after {
	z-index: 10; 
}

.twentytwenty-handle {
	height: 38px;
	width: 38px;
	position: absolute;
	left: 50%;
	top: 50%;
	margin-left: -20px;
	margin-top: -20px;
	border: 2px solid white;
	-webkit-border-radius: 1000px;
	-moz-border-radius: 1000px;
	border-radius: 1000px;
	-webkit-box-shadow: 0px 0px 12px rgba(51, 51, 51, 0.5);
	-moz-box-shadow: 0px 0px 12px rgba(51, 51, 51, 0.5);
	box-shadow: 0px 0px 12px rgba(51, 51, 51, 0.5);
	z-index: 40;
	cursor: pointer; 
}

.twentytwenty-horizontal .twentytwenty-handle:before {
	bottom: 50%;
	margin-bottom: 22px;
	-webkit-box-shadow: 0 3px 0 white, 0px 0px 12px rgba(51, 51, 51, 0.5);
	-moz-box-shadow: 0 3px 0 white, 0px 0px 12px rgba(51, 51, 51, 0.5);
	box-shadow: 0 3px 0 white, 0px 0px 12px rgba(51, 51, 51, 0.5); 
}
.twentytwenty-horizontal .twentytwenty-handle:after {
	top: 50%;
	margin-top: 22px;
	-webkit-box-shadow: 0 -3px 0 white, 0px 0px 12px rgba(51, 51, 51, 0.5);
	-moz-box-shadow: 0 -3px 0 white, 0px 0px 12px rgba(51, 51, 51, 0.5);
	box-shadow: 0 -3px 0 white, 0px 0px 12px rgba(51, 51, 51, 0.5); 
}

.twentytwenty-vertical .twentytwenty-handle:before {
	left: 50%;
	margin-left: 22px;
	-webkit-box-shadow: 3px 0 0 white, 0px 0px 12px rgba(51, 51, 51, 0.5);
	-moz-box-shadow: 3px 0 0 white, 0px 0px 12px rgba(51, 51, 51, 0.5);
	box-shadow: 3px 0 0 white, 0px 0px 12px rgba(51, 51, 51, 0.5); 
}
.twentytwenty-vertical .twentytwenty-handle:after {
	right: 50%;
	margin-right: 22px;
	-webkit-box-shadow: -3px 0 0 white, 0px 0px 12px rgba(51, 51, 51, 0.5);
	-moz-box-shadow: -3px 0 0 white, 0px 0px 12px rgba(51, 51, 51, 0.5);
	box-shadow: -3px 0 0 white, 0px 0px 12px rgba(51, 51, 51, 0.5); 
}

.twentytwenty-left-arrow {
	border-right: 6px solid white;
	left: 50%;
	margin-left: -17px; 
}

.twentytwenty-right-arrow {
	border-left: 6px solid white;
	right: 50%;
	margin-right: -17px; 
}

.twentytwenty-up-arrow {
	border-bottom: 6px solid white;
	top: 50%;
	margin-top: -17px; 
}

.twentytwenty-down-arrow {
	border-top: 6px solid white;
	bottom: 50%;
	margin-bottom: -17px; 
}


.twentytwenty-overlay {
	display: none;
}



.twentytwenty-handle {
	animation-name: pulse;
	animation-duration: 1s;
	animation-fill-mode: both;
	animation-iteration-count: infinite;
	transition: background-color 1s;
}

.twentytwenty-handle:hover {
	background-color: rgba(255,255,255,.3);
	transition: background-color .2s;
}

.twentytwenty-handle:active {
	animation-name: none;
}


@keyframes pulse {
	from {
		transform: scale3d(1, 1, 1);
	}

	50% {
		transform: scale3d(1.1, 1.1, 1.1);
	}

	to {
		transform: scale3d(1, 1, 1);
	}
}

.pulse {
	-webkit-animation-name: pulse;
	animation-name: pulse;
}